import React from "react";
import GradientContainer from "../theme/GradientContainer";
import {Box, Typography} from "@mui/material";
import GlassEffect from "../theme/GlassEffect";

const AboutUs: React.FC = () => {
    return (
        <>
            <GradientContainer>
                <GlassEffect sx={{marginBottom: '0vh'}}>
                    <Box
                        sx={{
                            borderRadius: 2,
                            boxShadow: 3,
                            p: 3,
                            maxWidth: 'lg',
                            margin: 'auto',
                            marginBottom: 'auto',
                            marginRight: '0.5vw'
                        }}
                    >
                        <Typography variant="h4" gutterBottom>
                            About Us
                        </Typography>
                        <Typography variant="body1">
                            We're building scalable server solutions for development teams who want to eliminate the
                            hassle of creating and managing server architecture. Our mission is to simplify the process
                            by providing easy-to-integrate tools that empower teams to focus on what matters
                            most—creating great products—without the complexity of traditional SDKs.
                            <br/><br/>
                            At the core of our approach is flexibility, efficiency, and a strong focus on security and
                            privacy. We understand that managing server infrastructure can be time-consuming and
                            complex, especially for teams who need to focus on delivering their projects. Whether you're
                            an indie game developer, a startup, or a student learning the ropes of development, we're
                            here to help remove the barriers of server management so you can focus on what truly
                            matters—innovation.
                            <br/><br/>
                            We prioritize the protection of your data and ensure that your team’s privacy and security
                            are always our top concern. Our solutions are built to safeguard sensitive information,
                            giving you peace of mind as you scale.
                            <br/><br/>
                            Currently, we're building our solution and planning to begin testing in the Netherlands once
                            it's ready. Our goal is to ensure our platform meets the needs of local creators first, but
                            we’re excited to expand globally as soon as possible.
                            <br/><br/>
                            Our goal is to enable creators—whether they're seasoned developers or students just starting
                            out—to focus on their passion, not the technical challenges of backend systems. By
                            simplifying server management, we empower teams to build better products faster, while we
                            take care of the complexity behind the scenes, with security and privacy always at the
                            forefront.
                            <br/><br/>
                            Join us in rethinking how server architecture should work. Let’s make building, deploying,
                            and scaling your products simpler, faster, more efficient—and most importantly, secure.
                        </Typography>
                    </Box>
                </GlassEffect>
            </GradientContainer>
        </>
    );
};

export default AboutUs;

